import Vue from 'vue'
import router from './router'
import App from './App'
import store from './store'
//bmob js-sdk
import './assets/js/key' 
// Mint UI
import { Swipe, SwipeItem,MessageBox,Search } from 'mint-ui';
import 'mint-ui/lib/style.css'
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Search.name, Search);


Vue.config.productionTip = false
// axios
import Axios from 'axios'
// axiosVue
Vue.prototype.$axios = Axios;

router.beforeEach((to, from, next) => {
  // 
  //
  let getFlag = localStorage.getItem("Flag");
  //isOnline
  if(getFlag === "isOnline"){
  
    //vuex
    store.state.isOnline = true;
    next();
  
  //
  }else{
    store.state.isOnline = false;
    //
    if(to.meta.isOnline){
      if(to.name == "cart"){
        //mint-ui
        MessageBox.alert('!').then(action => {
          //
          next({
            path: '/login-register'
          })
        });
      }
    }
  }
  // 
  switch(to.name){
    case 'home': store.commit('changeTitle','');break;
    case 'all': store.commit('changeTitle','');break;
    case 'cart': store.commit('changeTitle','');break;
    case 'mine': store.commit('changeTitle','');break;
    case 'details': store.commit('changeTitle','');break;
    case 'personal': store.commit('changeTitle','');break;
    case 'awaitPayment': store.commit('changeTitle','');break;
    case 'address': store.commit('changeTitle','');break;
    case 'collection': store.commit('changeTitle','');break;
    case 'loginRegister': store.commit('changeTitle','/'); break;
    case 'search': store.commit('changeTitle','');break;
    case 'account': store.commit('changeTitle','');break;
  }
  // 
  if(to.name == "home" || to.name == "sort" || to.name == "cart" || to.name == "mine" || to.name == "all" || to.name == "coconut" || to.name == "dried" || to.name == "flowers" || to.name == "guifei" || to.name == "haidun"|| to.name == "jinhuang" || to.name == "kaite" || to.name == "xiangya" || to.name == "loginRegister") {
    store.state.isMain = true;
  }else {
    store.state.isMain = false;
  }
  // 
  if(to.name == "details") {
    store.state.isDetail = true;
  }else {
    store.state.isDetail = false;
  }
  // 
  if(to.name == "account") {
    store.state.isAccount = true;
  }else {
    store.state.isAccount = false;
  }
  // 
  if (to.name == "details"|| to.name == "personal" || to.name == "address" || to.name == "collection" || to.name == "search" || to.name == "account" || to.name == "mineOrder" || to.name == "awaitPayment" || to.name == "awaitSendGoods" || to.name == "awaitReceivingGoods" || to.name == "completed" || to.name == "afterSale") {
    store.state.icon = true;
  }else {
    store.state.icon = false;
  }

  //
  if(to.name == "awaitPayment" || to.name == "awaitSendGoods" || to.name == "awaitReceivingGoods" || to.name == "completed" || to.name == "afterSale") {
    store.state.isOrder = true;
  }else {
    store.state.isOrder = false;
  }
  next();
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



